import React from "react";
import Button from "@material-ui/core/Button";
import '../books/BookPage.css'
import {validateSession, logout, getCookie} from '../Utility';

export default function Admin() {
    React.useEffect(() => {
        validateSession();
    }, []);

    return (
        <div className='BookPageContainer'>
            <h1> Administrtor </h1>
            {getCookie('username') ?
                <div className='AdminOptionContainer'>
                    <h2> {"Prihlsen ako " + getCookie('username')} </h2>
                    <Button fullWidth onClick={()=>(window.location = '/add')}> Prida knihu </Button>
                    <Button fullWidth onClick={()=>{window.location = '/gallery'}}> Upravi galriu </Button>
                    <Button fullWidth onClick={logout}> Odhlsi </Button>
                </div>
            :
                <Button fullWidth onClick={()=>(window.location = '/login')}> Prihlsi </Button>
            }
        </div>
    );
}
